import React from 'react'
import { Link } from 'gatsby'

const AllTagsTemplate = ({ pageContext: { tags } }) => {
  return (
    <div>
      <div>
        <ul>
          {tags.map((tag, index) => {
            return (
              <li key={index}>
                <Link to={`/tags/${tag}`}>{tag}</Link>
              </li>
            )
          })}
        </ul>
      </div>
    </div>
  )
}

export default AllTagsTemplate
